import axios, { AxiosPromise } from 'axios';
import { v1 as timestampUUID } from 'uuid';
import {
  Session,
  AtolResponce,
  TaskResultResponce,
  TaskResultStatus,
} from './types';

const DEFAULT_BASE_URL = 'http://127.0.0.1:16732';

export default (session: Session, baseURL: string = DEFAULT_BASE_URL) => {
  const API = axios.create({
    baseURL,
    timeout: 1000,
  });

  const { operator } = session;

  const post = (uuid: string, request: unknown): AxiosPromise<AtolResponce> => {
    return API.post('/api/v2/request', { uuid, request });
  };

  const get = (uuid: string): AxiosPromise<TaskResultResponce> => {
    return API.get(`/api/v2/request/${uuid}`);
  };

  const openShift = (): AxiosPromise<AtolResponce> => {
    const uuid = timestampUUID();
    return post(uuid, [
      {
        type: 'openShift',
        operator,
      },
    ]);
  };

  const checkStatus = async (uuid: string): Promise<TaskResultStatus> => {
    try {
      const {
        data: { results },
      } = await get(uuid);
      const status = results?.[0]?.status;

      return status;
    } catch (error) {
      console.log(error);
      return TaskResultStatus['error'];
    }
  };

  return { openShift, checkStatus };
};
